<template>
  <h1>
    <ContactForm>

      <!--  HEADER CONTENT  -->
      <div slot="header-content">
        <div class="theme-color full-width">
          <span class="title">{{ headerText }}</span>
          <!-- <span class="pl-3" style="font-size:30px">ðŸ‘‹</span> -->
          <v-icon class="theme-color pl-3" large>mdi-message-text</v-icon>
        </div>
        <div style="margin-bottom:-25px" class="theme-color">
          <span class="body-2">{{ subHeaderText }}</span>
        </div>
      </div>

      <!--  BODY CONTENT  -->
      <div slot="body-content">
        <v-row
          align="center"
          class="mx-2 my-2"
        >
        <v-card class="full-width white" style="margin-top:-60px">
          <v-card-title class="body-1 bold-text">{{ findInDocs }}</v-card-title>
          <v-card-text>
            <v-select
              v-model="choosedCategory"
              :color="selectColor"
              :items="docsCategories"
              outlined
              :label="category"
          ></v-select>
          </v-card-text>
          <v-card-actions class="mx-2" style="margin-top:-15px">
            <v-btn @click="toDocsMethod" text class="theme-bg-color">
              {{ toDocs }}
            </v-btn>
          </v-card-actions>
        </v-card>
        </v-row>

        <v-row
          align="center"
          class="mx-2 mt-5"
        >
        <v-card class="full-width white">
          <v-card-title class="body-1 bold-text">
            {{ contactSupport }}
          </v-card-title>
          <v-card-text>
            <div class="">
              <span class="body-2">{{ contactSupportSubText }}</span>
            </div>
          </v-card-text>
          <v-card-actions class="mx-2">
            <v-btn depressed @click="contactSupportMethod" class="theme-bg theme-color px-5">
              <v-icon class="theme-color pr-2" small>mdi-send</v-icon>
              {{ contactSupportBtnText }}
            </v-btn>
          </v-card-actions>
        </v-card>
        </v-row>
      </div>
    </ContactForm>
  </h1>
</template>

<script>
import ContactForm from '@/components/ContactForm'
import { mapActions, mapState } from 'vuex'

export default {
  name: 'Landing',
  components: {
    ContactForm
  },
  data: () => ({
    choosedCategory: ''
  }),
  computed: {
    ...mapState({
      config: ({ appState }) => appState.config,
      textKeys: ({ appState }) => appState.textKeys
    }),
    selectColor () {
      return getComputedStyle(document.body).getPropertyValue('--theme-bg')
    },
    docsCategories () {
      return (this.config.docsCategories && this.config.docsCategories.map(val => val.name)) || ['array with objects as- docsCategories: [{name, url}]']
    },
    headerText () {
      return this.textKeys.headerText || '{headerText}'
    },
    subHeaderText () {
      return this.textKeys.subHeaderText || '{subHeaderText}'
    },
    findInDocs () {
      return this.textKeys.findInDocs || '{findInDocs}'
    },
    contactSupport () {
      return this.textKeys.contactSupport || '{contactSupport}'
    },
    category () {
      return this.textKeys.category || '{category}'
    },
    toDocs () {
      return this.textKeys.toDocs || '{toDocs}'
    },
    contactSupportSubText () {
      return this.textKeys.contactSupportSubText || '{contactSupportSubText}'
    },
    contactSupportBtnText () {
      return this.textKeys.contactSupportBtnText || '{contactSupportBtnText}'
    }
  },
  methods: {
    ...mapActions([]),
    contactSupportMethod () {
      this.$router.push({ path: '/contact', query: this.$route.query })
    },
    toDocsMethod () {
      const { choosedCategory } = this
      let breaked = false
      if (this.config.docsCategories) {
        for (const c in this.config.docsCategories) {
          const category = this.config.docsCategories[c]
          if (category.name === choosedCategory) {
            console.log(category.url)
            window.open(category.url)
            breaked = true
            break
          }
        }
      }
      !breaked && window.open('https://docs.paymentiq.io/europe')
    }
  }
}
</script>
